  position: relative;
  display: block;
  height:70px;
  .nav-bar{
    height:70px;
    line-height:70px;
    border-top:1px solid $colorH;
    background-color:$colorG;
    z-index:8;
    position: relative;
    &.is_fixed{
      position:fixed;
      top:0;
      width:100%;
